import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { EditorState, convertToRaw } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { v4 as uuidv4 } from "uuid";
import { useEffect, useState } from "react";
import { Editor } from "react-draft-wysiwyg";
import { useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
  Spinner,
  CustomInput,
} from "reactstrap";
import validateOptions from "../../../../../constants/validate";
import {
  convertFileToBase64,
  convertFileToBlob,
  uploadImage,
  uploadVideo,
  uploadVideoChunk,
} from "../../../../../helper/common";
import avatarBlank from "../../../../../assets/images/avatars/bg-blank.png";
import { update } from "../store/action";
import { htmlToDraftUtil } from "@utils";
import { scrollToTop } from "../../../../../utility/Utils";
import { toast } from "react-toastify";
import { Trash2 } from "react-feather";
const styleFormVideo = {
  borderColor: "#404656",
};

const styleTextVideo = {
  color: "#b4b7bd",
};

const TermTab = ({ selected, common, intl }) => {
  const { register, errors, handleSubmit } = useForm();
  const dispatch = useDispatch();
  const { id } = useParams();
  const store = useSelector((state) => state.terms);
  const [disable, setDisable] = useState(false);
  const lang = useSelector((state) => state.common.language);
  const history = useHistory();
  const productId = useSelector((state) => selected.productId);
  const [termData, setTermData] = useState(null);
  const [selectedFile, setSelectedFile] = useState();
  const [File, setFile] = useState();

  const [videoUrl, setVideoUrl] = useState(null);

  const [mediaurl, setMediaUrl] = useState();
  const [mediaurl2, setMediaUrl2] = useState();
  const [mediaurl3, setMediaUrl3] = useState();
  const [mediaurl4, setMediaUrl4] = useState();
  const [mediaurl5, setMediaUrl5] = useState();

  const handleFileSelect = async (event) => {
    const file = event.target.files[0];
    const imageBase64 = await convertFileToBase64(file);
    setFile(file);
    setSelectedFile(imageBase64);
  };
  useEffect(() => {
    (async () => {
      if (selectedFile) {
        const newData = selectedFile.replace(/,/gi, "").split("base64");
        if (newData[1]) {
          const data = {
            imageData: newData[1],
            imageFormat: "pdf",
          };
          await uploadImage(data, File).then((res) => {
            const fileurl = document.getElementById("fileurl");
            fileurl.value = res?.data;
          });
        }
      }
    })();
  }, [selectedFile, File]);

  const TermOptions = validateOptions.TermOptions;

  const [termContent, setContent] = useState(EditorState.createEmpty());

  useEffect(() => {
    setContent(htmlToDraftUtil(store.selected?.content || " "));
  }, [store]);

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err, id]);

  const uploadCallback = async (file) => {
    var linkImg = avatarBlank;
    const imageBase64 = await convertFileToBase64(file);
    if (imageBase64) {
      const newData = imageBase64.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        const data = {
          imageData: newData[1],
          imageFormat: file?.type?.split("/")[1] || "png",
        };
        await uploadImage(data, file).then((res) => {
          linkImg = res?.data;
        });
      }
    }
    return new Promise((resolve, reject) => {
      resolve({ data: { link: linkImg } });
    });
  };

  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null && termData !== null && selected.id !== termData.id)
    ) {
      setTermData(selected);
      if (selected?.mediaurl) {
        const parseUrl = JSON.parse(selected?.mediaurl || "[]");
        setVideoUrl(parseUrl);
      }
    }
  }, [selected, termData]);

  useEffect(() => {
    if (selected !== null) {
      if (selected?.mediaurl) {
        const parseUrl = JSON.parse(selected?.mediaurl || "[]");
        if (parseUrl && parseUrl?.length > 0) {
          parseUrl.forEach((item, index) => {
            switch (index) {
              case 0:
                setMediaUrl(item);
                break;
              case 1:
                setMediaUrl2(item);
                break;
              case 2:
                setMediaUrl3(item);
                break;
              case 3:
                setMediaUrl4(item);
                break;
              case 4:
                setMediaUrl5(item);
                break;
              default:
                break;
            }
          });
        }
      }
    }

    return () => {
      setMediaUrl();
      setMediaUrl2();
      setMediaUrl3();
      setMediaUrl4();
      setMediaUrl5();
    };
  }, [selected]);

  useEffect(() => {
    if (store.status === 200) {
      setDisable(false);
      scrollToTop();
      history.goBack();
    }
  }, [store.status, history, id]);

  const onSubmit = async (values) => {
    setDisable(true);
    const drafttermcontent = draftToHtml(
      convertToRaw(termContent.getCurrentContent())
    );

    const file = values?.mediaurl[0];
    const file2 = values?.mediaurl2[0];
    const file3 = values?.mediaurl3[0];
    const file4 = values?.mediaurl4[0];
    const file5 = values?.mediaurl5[0];

    const dataUpload = [];

    file ? dataUpload.push(file) : mediaurl && dataUpload.push(mediaurl);
    file2 ? dataUpload.push(file2) : mediaurl2 && dataUpload.push(mediaurl2);
    file3 ? dataUpload.push(file3) : mediaurl3 && dataUpload.push(mediaurl3);
    file4 ? dataUpload.push(file4) : mediaurl4 && dataUpload.push(mediaurl4);
    file5 ? dataUpload.push(file5) : mediaurl5 && dataUpload.push(mediaurl5);

    const promiseUpload = dataUpload.map((item) => {
      return new Promise(async (resolve, reject) => {
        if (typeof item === "string") {
          resolve(item);
        } else {
          const file = item;
          const chunkSize = 10 * 1024 * 1024;
          const totalChunks = Math.ceil(file.size / chunkSize);
          const uuId = uuidv4();
          for (let chunkIndex = 0; chunkIndex < totalChunks; chunkIndex++) {
            const start = chunkIndex * chunkSize;
            const end = Math.min(start + chunkSize, file.size);
            const chunk = file.slice(start, end);
            await uploadVideoChunk(
              chunk,
              chunkIndex,
              totalChunks,
              uuId,
              file.type
            ).then((res) => {
              if (res && res?.data && chunkIndex + 1 === totalChunks) {
                resolve(res?.data);
              }
            });
          }
        }
      });
    });

    Promise.all(promiseUpload).then(async (resVideo) => {
      dispatch(
        update({
          id: parseInt(id),
          data: {
            sort_order: parseInt(values.sort_order),
            lang: lang,
            title: values.title,
            description: values.description,
            mediaurl: JSON.stringify(resVideo || ""),
            content: drafttermcontent,
            productId: productId,
            contenturl: values.fileurl,
            status: values.status,
            progress: 1,
          },
        })
      );
    });
  };

  const checkSizeVideo = (name, size) => {
    if (size > 400485760) {
      setDisable(true);
      toast.error(
        <>
          {name} <FormattedMessage id="Maximum image size is only 400MB" />
        </>
      );
      return false;
    } else {
      if (disable) {
        setDisable(false);
      }
      return true;
    }
  };

  const handleFileChange = (e, setMedia) => {
    const file = e.target.files[0];

    const checkSize = checkSizeVideo(file?.name, Number(file?.size));

    if (checkSize) {
      if (file) {
        setMedia(file.name);
      } else {
        setMedia("");
      }
    }
  };

  const handleClearFile = (setMedia, name) => {
    const inputFile = document.getElementById(name);
    setMedia("");
    inputFile.value = "";
  };

  return (
    <Form onSubmit={handleSubmit(onSubmit)}>
      <FormGroup>
        <Label for="title">
          <FormattedMessage id="Title" /> <span className="text-danger">*</span>
        </Label>
        <Input
          name="title"
          id="title"
          placeholder=" "
          defaultValue={termData && termData.title}
          innerRef={register(TermOptions.title)}
          onBlur={() => {
            let title = document.getElementById("title");
            if (title && title.value) {
              title.value = title.value.trim();
            }
          }}
          className={classNames({ "is-invalid": errors["title"] })}
        />
        <small className="text-danger">
          {errors?.title && errors.title.message}
        </small>
      </FormGroup>

      <FormGroup>
        <Label for="sort_order">
          <FormattedMessage id="sort_order" />{" "}
        </Label>
        <Input
          name="sort_order"
          id="sort_order"
          placeholder=" "
          defaultValue={termData && termData.sort_order}
          innerRef={register(TermOptions.sort_order)}
          onBlur={() => {
            let sort_order = document.getElementById("sort_order");
            if (sort_order && sort_order.value) {
              sort_order.value = sort_order.value.trim();
            }
          }}
          className={classNames({ "is-invalid": errors["sort_order"] })}
        />
        <small className="text-danger">
          {errors?.sort_order && errors.sort_order.message}
        </small>
      </FormGroup>

      {/* TODO add description and content */}
      <FormGroup>
        <Label for="description">
          <FormattedMessage id="description" />{" "}
          <span className="text-danger"></span>
        </Label>
        <Input
          name="description"
          id="descriptionss"
          placeholder=""
          innerRef={register(TermOptions.description)}
          defaultValue={termData && termData.description}
          onBlur={() => {
            let description = document.getElementById("description");
            if (description && description.value) {
              description.value = description.value.trim();
            }
          }}
          className={classNames({ "is-invalid": errors["description"] })}
        />
        <small className="text-danger">
          {errors?.title && errors.title.message}
        </small>
      </FormGroup>
      <FormGroup>
        <Label for="termContent">
          <FormattedMessage id="Content" />{" "}
          <span className="text-danger"></span>
        </Label>
        <Editor
          toolbar={{
            // options: ["inline", "textAlign"],
            // inline: {
            //   inDropdown: false,
            //   options: ["bold", "italic", "underline"],
            // },
            image: { uploadCallback: uploadCallback },
          }}
          stripPastedStyles={true}
          name="termContent"
          editorState={termContent}
          wrapperClassName="content_term_edit"
          onEditorStateChange={(data) => setContent(data)}
          innerRef={register(TermOptions.content)}
          defaultEditorState={termContent}
        />
        <small className="text-danger">
          {errors?.content && errors.content.message}
        </small>
      </FormGroup>

      <FormGroup>
        <Label for="fileurl">
          <FormattedMessage id="fileurl" />{" "}
        </Label>
        <Input
          name="fileurl"
          id="fileurl"
          placeholder=""
          defaultValue={termData && termData.contenturl}
          innerRef={register(TermOptions.fileurl)}
          onBlur={() => {
            let fileurl = document.getElementById("fileurl");
            if (fileurl && fileurl.value) {
              fileurl.value = fileurl.value.trim();
            }
          }}
          className={classNames({ "is-invalid": errors["fileurl"] })}
        />
        <small className="text-danger">
          {errors?.fileurl && errors.fileurl.message}
        </small>
        {errors?.fileurl?.type == "validate" && (
          <small className="text-danger">
            <FormattedMessage id="Invalid fileurl" />
          </small>
        )}
      </FormGroup>
      <FormGroup>
        <Input
          type="file"
          name="fileurl"
          id="fileurl"
          onChange={handleFileSelect}
          className={classNames({ "is-invalid": errors["fileurl"] })}
        />
        <small className="text-danger">
          {errors?.fileurl && errors.fileurl.message}
        </small>
        {errors?.fileurl?.type == "validate" && (
          <small className="text-danger">
            <FormattedMessage id="Invalid fileurl" />
          </small>
        )}
      </FormGroup>

      {/* Video 1 */}
      <FormGroup>
        <Label for="mediaurl">
          <Label for="video">
            <FormattedMessage id="mediaurl" />{" "}
          </Label>
          <span className="text-danger"></span>
        </Label>
      </FormGroup>

      <FormGroup>
        <div>
          <input
            type="file"
            id="mediaurl"
            name="mediaurl"
            ref={(e) => {
              register(e);
            }}
            style={{ display: "none" }}
            onChange={(e) => handleFileChange(e, setMediaUrl)}
          />
          <div className="form-control" style={styleFormVideo}>
            {mediaurl ? (
              <div className="d-flex justify-content-between align-items-center w-100 h-100">
                <div style={styleTextVideo}>{mediaurl}</div>
                <div>
                  <button
                    type="button"
                    className="btn btn-link"
                    onClick={() => handleClearFile(setMediaUrl, "mediaurl")}
                  >
                    <Trash2 size={20} color="#ea5455" />
                  </button>
                </div>
              </div>
            ) : (
              <label className="w-100 h-100" htmlFor="mediaurl">
                Choose file
              </label>
            )}
          </div>
        </div>
      </FormGroup>

      {/* Video 2 */}
      <FormGroup>
        <div>
          <input
            type="file"
            id="mediaurl2"
            name="mediaurl2"
            ref={(e) => {
              register(e);
            }}
            style={{ display: "none" }}
            onChange={(e) => handleFileChange(e, setMediaUrl2)}
          />
          <div className="form-control" style={styleFormVideo}>
            {mediaurl2 ? (
              <div className="d-flex justify-content-between align-items-center w-100 h-100">
                <div style={styleTextVideo}>{mediaurl2}</div>
                <div>
                  <button
                    type="button"
                    className="btn btn-link"
                    onClick={() => handleClearFile(setMediaUrl2, "mediaurl2")}
                  >
                    <Trash2 size={20} color="#ea5455" />
                  </button>
                </div>
              </div>
            ) : (
              <label className=" w-100 h-100" htmlFor="mediaurl2">
                Choose file
              </label>
            )}
          </div>
        </div>
      </FormGroup>

      {/* Video 3 */}
      <FormGroup>
        <div>
          <input
            type="file"
            id="mediaurl3"
            name="mediaurl3"
            ref={(e) => {
              register(e);
            }}
            style={{ display: "none" }}
            onChange={(e) => handleFileChange(e, setMediaUrl3)}
          />
          <div className="form-control" style={styleFormVideo}>
            {mediaurl3 ? (
              <div className="d-flex justify-content-between align-items-center w-100 h-100">
                <div style={styleTextVideo}>{mediaurl3}</div>
                <div>
                  <button
                    type="button"
                    className="btn btn-link"
                    onClick={() => handleClearFile(setMediaUrl3, "mediaurl3")}
                  >
                    <Trash2 size={20} color="#ea5455" />
                  </button>
                </div>
              </div>
            ) : (
              <label className=" w-100 h-100" htmlFor="mediaurl3">
                Choose file
              </label>
            )}
          </div>
        </div>
      </FormGroup>

      {/* Video 4 */}
      <FormGroup>
        <div>
          <input
            type="file"
            id="mediaurl4"
            name="mediaurl4"
            ref={(e) => {
              register(e);
            }}
            style={{ display: "none" }}
            onChange={(e) => handleFileChange(e, setMediaUrl4)}
          />
          <div className="form-control" style={styleFormVideo}>
            {mediaurl4 ? (
              <div className="d-flex justify-content-between align-items-center w-100 h-100">
                <div style={styleTextVideo}>{mediaurl4}</div>
                <div>
                  <button
                    type="button"
                    className="btn btn-link"
                    onClick={() => handleClearFile(setMediaUrl4, "mediaurl4")}
                  >
                    <Trash2 size={20} color="#ea5455" />
                  </button>
                </div>
              </div>
            ) : (
              <label className=" w-100 h-100" htmlFor="mediaurl4">
                Choose file
              </label>
            )}
          </div>
        </div>
      </FormGroup>

      {/* Video 5 */}
      <FormGroup>
        <div>
          <input
            type="file"
            id="mediaurl5"
            name="mediaurl5"
            ref={(e) => {
              register(e);
            }}
            style={{ display: "none" }}
            onChange={(e) => handleFileChange(e, setMediaUrl5)}
          />
          <div className="form-control" style={styleFormVideo}>
            {mediaurl5 ? (
              <div className="d-flex justify-content-between align-items-center w-100 h-100">
                <div style={styleTextVideo}>{mediaurl5}</div>
                <div>
                  <button
                    type="button"
                    className="btn btn-link"
                    onClick={() => handleClearFile(setMediaUrl5, "mediaurl5")}
                  >
                    <Trash2 size={20} color="#ea5455" />
                  </button>
                </div>
              </div>
            ) : (
              <label className=" w-100 h-100" htmlFor="mediaurl5">
                Choose file
              </label>
            )}
          </div>
        </div>
      </FormGroup>

      <FormGroup>
        <Label for="status">
          <FormattedMessage id="status" />
        </Label>
        <Input
          type="select"
          name="status"
          id="status"
          defaultValue={termData && termData.status}
          innerRef={register({ required: true })}
        >
          <option value="1">{intl.formatMessage({ id: "Active" })}</option>
          <option value="0">{intl.formatMessage({ id: "Deactive" })}</option>
        </Input>
      </FormGroup>
      <div style={{ textAlign: "end" }}>
        <Button
          type="submit"
          className="mr-1"
          color="primary"
          disabled={disable}
        >
          {disable ? <Spinner size="sm" /> : <FormattedMessage id="update" />}
        </Button>
        <Button
          type="reset"
          color="secondary"
          outline
          onClick={() => history.goBack()}
        >
          <FormattedMessage id="Cancel" />
        </Button>
      </div>
    </Form>
  );
};
export default injectIntl(TermTab);
